import sha1 from 'sha1'
import {Base64} from 'js-base64'
const  baseUrl='https://www.apiopen.top';
export default {
	post(url,data,success,fail){
		console.log(sha1("message"))
		console.log(Base64.encode('dankogai'))
		uni.request({
			url: baseUrl + url,
			data: data,
			method: 'POST',
			header: {
				 'Content-Type': 'application/x-www-form-urlencoded',
				// "Content-Type": "application/json"
			},
			// dataType: 'json',
			success: function (res) {
				console.log(res)
				if(res.statusCode==200){
					let result=res.data
					console.log(result)
					return success(result)
				}else{
					uni.showToast({
						title: res.data.message
					});
				}
			},
			fail: function (res) {
				console.log(res)
				uni.showToast({
					icon:'none',
					title: res.errMsg
				});
			}
		})
	},
	get (url,data,success,fail) {
		uni.request({
			url: baseUrl + url,
			data: data,
			method: 'GET',
			header: {
				 // 'Content-Type': 'application/x-www-form-urlencoded',
				"Content-Type": "application/json"
			},
			success: function (res) {
				console.log(res)
				if(res.statusCode==200){
					let result=res.data
					return success(result)
				}else{
					uni.showToast({
						title: res.data.message
					});
				}
			},
			fail: function (res) {
				console.log(res)
				uni.showToast({
					title: res.message
				});
			}
		})
	}
}
